.button {
    border-radius: 3px;
    color: var(--light-140, #FFF);
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    /* 142.857% */
    padding: 5px 24px;
}

.disabled {
    opacity: 0.5;
    background: #004D99;
    color: #002E5C;
}

.secondary {
    border: 1px solid #06C;
    color: #06C;
    background-color: #fff;
}

.default {
    background: var(--primary-100, #06C);
}

.red {
    background: var(--danger-100, #CC1439);
}

.default:hover {}

.red:hover {}

.secondary:hover {}


.default:active {}

.secondary:active {}

.red:active {}

.button:disabled {}